import React, { useState } from 'react';
import "./contact.css";
import logo from "../../images/logo.png";
import CallIcon from '@mui/icons-material/Call';
import EmailIcon from '@mui/icons-material/Email';
import PinDropIcon from '@mui/icons-material/PinDrop';
import CopyrightIcon from '@mui/icons-material/Copyright';
import FacebookIcon from '@mui/icons-material/Facebook';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import InstagramIcon from '@mui/icons-material/Instagram';
import axios from "axios";

const Contact = () => {
  const [data, setData] = useState({
    name:"",
    email:"",
    message:""
  });
  const eHandler = (e) => {
    e.preventDefault();
    const {name, value} = e.target;
    setData({
      ...data,
      [name]:value
    })
  };
  const sendData = async (e) => {
    const {name, message, email} = data;
    if(name && email && message){
      const res = await axios.post(process.env.REACT_APP_SERVER_URL,data);
      window.alert("Data send")
    }
  }

  return (
    <div className='contactContainer' id='contactPage'>
        <div>
          <div className='contactContainerHeading'>Contact</div>
          <div className='contactContainerContDisp'>
              <div>
                <div className='a'>
                  <span>Drop Me a Message</span>
                  <p>Please tell me any information or thinking you about this
                    web-page.<br/>
                    Any question asking me, Please call me bellow number or fill details in send box and
                    click send button, I respone you whithin few times.
                  </p>
                  <div><span><CallIcon/></span>+91 7271052076</div>
                  <div><span><EmailIcon/></span>amitkumarmaurya511@gmail.com</div>
                  <div><span><PinDropIcon/></span>Pakari Motihan Prayagraj 221508</div>
                </div>
              </div>
              <div>
                <div>
                  <form onSubmit={sendData}>
                    <input type='text' placeholder='Name' name='name' value={data.name} onChange={eHandler} required autoComplete='false'/>
                    <input type='email' placeholder='Email' name='email' value={data.email} onChange={eHandler} required autoComplete='false'/>
                    <textarea rows="5" cols="30" placeholder='Message' name='message' onChange={eHandler} value={data.messssage} required autoComplete='false'></textarea>
                    <input type='submit' value='Send' />
                  </form>
                </div>
              </div>
          </div>
          <div className='ContactFooterPannel'>
            <hr/>
            <div>
                <div><img src={logo} alt='not found' /></div>
                <span>
                  <div><CopyrightIcon id="materialCopyLogo"/>2024-2025 - AK.MernStack Developer,All rights reserved</div>
                </span>
                <div>
                  <div><FacebookIcon className='mateClasslogo'/></div>
                  <div><InstagramIcon className='mateClasslogo'/></div>
                  <div><LinkedInIcon className='mateClasslogo'/></div>
                </div>
            </div>
          </div>
        </div>
    </div>
  )
}

export default Contact;