import React from 'react';
import "./services.css";
import desktop from "../../images/d.jpg";
import mobile from "../../images/m.png";
import programming from "../../images/p.jpg";
import video from "../../images/v.jpg";

const Services = () => {
  return (
    <div className='serviceContainer'>
        <div>SERVICES</div>
        <div>
            <div className='serviceContainer_firstflex' id='servicesPage'>
                <div>
                    <div>
                        <img src={video} alt='not found'/>
                        <span>VIDEO EDITING</span>
                        <div>As an editor, I know how important it is to get the message across clearly—that's why I pride myself on being able to not only edit footage but also create graphics and animations to help tell the story that needs to be told.</div>
                        </div>
                </div>
                <div>
                    <div>
                        <img src={mobile} alt='not found'/>
                        <span>MOBILE APP<br/>Development</span>
                        <div>Mobile application development is the process of creating software applications that run on a mobile device, and a typical mobile application utilizes a network connection to work with remote computing resources</div>
                        </div>
                </div>
                </div>
            <div className='serviceContainer_secondflex'>
                <div>
                    <div>
                        <img src={desktop} alt='not found'/>
                        <span>DESKTOP APP DEVELOPMENT</span>
                        <div>Creating an application locally for computer devices (operating systems) is referred to as desktop application development. Desktop app developers build these applications that are used specifically on laptops and desktops. These applications are usually built for Linux, Microsoft Windows, and macOS</div>
                        </div>
                </div>
                <div>
                    <div>
                        <img src={programming} alt='not found'/>
                        <span>PROGRAMMING LANGUAGES</span>
                        <div>
                                <span>Java</span><br/>
                                <span>Java Script</span><br/>
                                <span>Python</span><br/>
                                <span>C</span><br/>
                                <span>C++</span><br/>
                                <span>C#</span><br/>
                            </div>
                        </div>
                </div>
            </div>
        </div>
    </div>
  )
}

export default Services;