import React from 'react';
import "./aboutMe.css";
import photo from "../../../images/amit.jpg";
import resume from "../../../images/resume.jpg";

const AboutMe = () => {
  const resumeClick = () => {
    const change = document.getElementById("imgPoto");
    change.src = resume;
    change.style.position = "absolute";
    change.style.width = "400px"
    change.style.height = "500px";
    setTimeout(()=>{
      change.src = photo;
      change.removeAttribute("style");
    },10000);
  }
  const hireBtn = () => {
    window.alert("Please go contact section");
  }
  return (
    <div className='abourMeContainer' id='aboutPage'>
        <div>
            <div><span><img src={photo} alt='not Found' id='imgPoto'/></span></div>
            <div>
                <h1>ABOUT ME</h1>
                <p>My name is Amit kumar Maurya from prayagraj. I have completed BCA from Shubharti university meerut uttar pradesh.
                  And MCA is not completed yet.<br/>
                  I have good knowledge  of C, C++, HTML, javaScript, Css, css3,bootstrap, nodejs, react, mongodb, expressjs<br/>
                  Along with technical skills, I have good communication skills that I fell are necessary to accomplish daily responsiblities.<br/>
                  I am also 9 months training experience at Arudan Technology Pvt. Ltd company.
                </p>
                <div><button onClick={hireBtn}>HIRE ME</button><button onClick={resumeClick}>RESUME</button></div>
            </div>
        </div>
    </div>
  )
}

export default AboutMe