import React from 'react';
import "./portfolio.css";
import PortfolioComponents from "../portfolio/portfolioComponents/PortfolioComponents";
import NavigationBar from './portfolioComponents/NavigationBar';


const Portfolio = () => {
  return (
    <div className='portfolioContainer' id='portfolioPage'>
        <div>PORTFOLIO</div>
        <NavigationBar />
        <div>
            <PortfolioComponents/>
        </div>
    </div>
  )
}

export default Portfolio