import React from 'react';
import "./experience.css";
import c from "../../images/c.png";
import cplus from "../../images/c++.png";
import css from "../../images/css.png";
import html from "../../images/html.png";
import js from "../../images/js.jpeg";
import mongo from "../../images/mongo.png";
import nodejs from "../../images/nodejs.png";
import reactjs from "../../images/react.webp";
import sql from "../../images/sql.png";

const Experience = () => {
  return (
    <div className='experienceContainer' id='experiencePage'>
        <div>
            EXPERIENCE
        </div>
        <div className='experienceContainer_firstflex'>
            <div><img src={mongo} alt=''/><span>MongoDB</span></div>
            <div><img src={css} alt=''/><span>Css</span></div>
            <div><img src={html} alt=''/><span>Html</span></div>
            <div><img src={nodejs} alt=''/><span>Node.Js</span></div>
            <div><img src={reactjs} alt=''/><span>React.Js</span></div>
        </div>
        <div className='experienceContainer_firstflex'>
            <div><img src={sql} alt=''/><span>Sql</span></div>
            <div><img src={js} alt=''/><span>JavaScript</span></div>
            <div><img src={c} alt=''/><span>C</span></div>
            <div><img src={cplus} alt=''/><span>C++</span></div>
        </div>
    </div>
  )
}

export default Experience;