import React, { useEffect, useState } from 'react';
import "./portfolioCompo.css";
import axios from "axios";

const PortfolioContainer = () => {
  const [data, setData] = useState();
  const getProjectData = async () => {
    const res = await axios.get(`${process.env.REACT_APP_PORTFOLIO_GET_URL}`);
    console.log(res.data.data)
    setData(res.data.data);
  }
  useEffect(()=> {
    getProjectData();
  },[])
  return (
    <div>
      {data ? (data.map((abc,index)=>{return(
        <div className='portfolioComp_container' key={abc._id}>
        <div>
        <div>
          <h1>{`Project ${index}`}</h1>
          <h2>{abc.name}</h2>
          <p>
            {abc.sub_description}
          </p>
          <div><button>Read more</button></div>
      </div>
        <div><span><img src={`${process.env.REACT_APP_IMG_URL_PORTFOLIO}/${abc.image}`} alt='not Found'/></span></div>
    </div>
    </div>)}) ) : (<div className='portfolioComp_container'>Data Not Found</div>)}
        </div> 
    
  )
}

export default PortfolioContainer;