import React from 'react';
import "./navigation.css";
import logo from "../../../src/images/logo.png";

const NavigationBar = () => {
  return (
    <div className='navigationBarContainer'>
      <div>
          <span><img src={logo} alt=''/></span>
        <div className='navigationBarmenuIcon'>
          <span><a href='#homePage'>HOME</a></span>
          <span><a href='#aboutPage'>ABOUT ME</a> </span>
          <span><a href='#portfolioPage'>PORTFOLIO</a></span>
          <span><a href='#servicesPage'>SERVICES</a></span>
          <span><a href='#experiencePage'>EXPERIENCE</a></span>
          <span><a href='#contactPage'>CONTACT</a></span>
        </div>
      </div>
    </div>
  )
}

export default NavigationBar;